* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  position: relative;
  display: grid;
  place-content: center;
  min-height: 100vh;
  font-family: "Outfit", sans-serif;
  background-color: #333;
  background-color: #d6e2f0;
}

.attribution {
  position: absolute;
  font-size: 15px;
  text-align: center;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

.attribution a {
  color: #3e52a3;
}

.card {
  max-width: 320px;
  padding: 15px;
  background-color: white;
  border-radius: 20px;
  box-shadow: 0 2px 5px 5px #7b879d;
  box-shadow: 0 1px 10px 10px rgba(170, 170, 170, 0.1);
}
.card__image {
  max-width: 290px;
  margin-bottom: 30px;
}
.card img {
  max-width: 100%;
  border-radius: 15px;
}
.card__title {
  font-size: 22px;
  text-align: center;
  font-weight: 700;
  color: #1f3251;
  margin-block-end: 25px;
}
.card__info {
  font-size: 15px;
  text-align: center;
  color: #7b879d;
  max-width: 90%;
  margin: 0 auto;
}